@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  font-size: 16px;
  font-family: 'Open Sans';
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
  --transition-speed: 600ms;
  --fa-primary: #eb7900;  
  --fa-secondary: #f0d21e;
  --theme-hell-bg: white;
  --theme-hell-color: black;
  --theme-dunkel-bg: #343a40;
  --theme-dunkel-color: #f8f9fa;
  --theme-bg: var(--theme-hell-bg);
  --theme-color: var(--theme-hell-color);
}

html body {
  color: black;
  color: var(--theme-color);
  background-color: white;
  background-color: var(--theme-bg);
  margin: 0;
  padding: 0;
}

.table-hover tbody tr:hover {
  color: black;
  color: var(--theme-color);
  background-color: rgba(0, 0, 0, 0.3);
}

.table {
  color: black;
  color: var(--theme-color);
}

body::-webkit-scrollbar {
  width: 0.25rem;
}

body::-webkit-scrollbar-track {
  background: #1e1e24;
}

body::-webkit-scrollbar-thumb {
  background: #6649b8;
}

main {
  margin-left: 5rem;
  padding: 1rem;
}

.purple {
  --fa-primary: #ff7eee;  
  --fa-secondary: #df49a6; 
}

.dark {
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
}

.light {
  --text-primary: #1f1f1f;
  --text-secondary: #000000;
  --bg-primary: #ffffff;
  --bg-secondary: #e4e4e4;
}

.solar {
  --text-primary: #576e75;
  --text-secondary: #35535c;
  --bg-primary: #fdf6e3;
  --bg-secondary: #f5e5b8;
}

.theme-icon {
  display: none;
}

.dark #darkIcon {
  display: block;
}

.light #lightIcon {
  display: block;
}

.solar #solarIcon {
  display: block;
}

/*          */
.info-data {
  border: none !important;
}

.info-panel {
  display: flex;
  align-items: center;
  height: 100%;
}

.input-row {
  padding: 0 !important;
}

.add-icon {
  height: 38px;
  -webkit-filter: grayscale(100%) opacity(0.7);
          filter: grayscale(100%) opacity(0.7);
  transition: 600ms;
  transition: var(--transition-speed);
}

.add-icon:hover {
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
  cursor: pointer;
}

.confirm-row {
  display: flex;
}

/* Not Large screens */
@media only screen and (max-width: 992px) {
  .large-column {
    display: none;
  }
}

/* Large Screens */
@media only screen and (min-width: 992px) {

}
.sidebar {
  position: fixed;
  background-color: var(--bg-primary);
  transition: width 600ms ease;
  overflow: hidden;
}

.sidebar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.nav-item {
  width: 100%;
}

.nav-item:last-child {
  margin-top: auto;
}

.nav-link {
  display: flex;
  align-items: center;
  height: 5rem;
  color: var(--text-primary);
  text-decoration: none;
  -webkit-filter: grayscale(100%) opacity(0.7);
          filter: grayscale(100%) opacity(0.7);
  transition: var(--transition-speed);
  cursor: pointer;
}

.nav-link:hover {
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
  background: var(--bg-secondary);
  color: var(--text-secondary);
}

.link-text {
  display: none;
  margin-left: 1rem;
}

.nav-link svg {
  width: 2rem;
  min-width: 2rem;
  margin: 0 0.5rem;
}

.fa-primary {
  color: var(--fa-primary);
}

.fa-secondary {
  color: var(--fa-secondary);
}

.fa-primary,
.fa-secondary {
  transition: var(--transition-speed);
}

.logo {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  color: var(--text-secondary);
  background: var(--bg-secondary);
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.logo svg {
  transform: rotate(0deg);
  transition: var(--transition-speed);
}

.logo-text
{
  display: inline;
  position: absolute;
  left: -999px;
  transition: var(--transition-speed);
}

.sidebar:hover .logo svg {
  transform: rotate(-180deg);
}

/* Small screens */
@media only screen and (max-width: 600px) {
  .sidebar {
    bottom: 0;
    width: 100vw;
    height: 5rem;
  }

  .logo {
    display: none;
  }

  .sidebar-nav {
    flex-direction: row;
  }

  .nav-link {
    justify-content: center;
  }

  main {
    margin: 0;
  }
}

/* Large screens */
@media only screen and (min-width: 600px) {
  .sidebar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }

  .sidebar:hover {
    width: 16rem;
  }

  .sidebar:hover .link-text {
    display: inline;
  }

  .sidebar:hover .logo svg
  {
    margin-left: 11rem;
  }

  .sidebar:hover .logo-text
  {
    left: 0px;
  }
}
.tableFixHead          { overflow-y: auto; height: 100%; }
.tableFixHead thead th { position: -webkit-sticky; position: sticky; top: 0; }

table  { border-collapse: collapse; width: 100%; }

.selected-row {
  background-color: var(--fa-primary) !important;
      /* Firefox */
      /* WebKit */
      /* Opera */
      /* Standard */
      transition: all 200ms ease-in;
}
.modal-dialog .modal-content {
  color: black;
}

.sorted-header {
  border: none !important;
}
.interest-data {
  border: none !important;
  padding: 0.25rem !important;
}

.input-text {
  text-align: right;
}

.interest-table {
  table-layout: fixed;
}

.interest-table th {
  width: 3rem;
  text-align: center;
}

.interest-table th+th {
  width: auto;
}

.interest-table td {
  text-align: center;
}

.interest-table td+td {
  text-align: right;
}

.interest-table-wrapper {
  min-width: 992px;
  
}
